.btn {
    display: block;
    background: var(--darkGrey);
    width: 25%;
    min-width: 200px;
    height: 60px;
    border-radius: 30px;
    color: var(--white);
    border: 0;
    font-size: var(--fontBig);
    margin: 20px auto;
    outline: none;
    cursor: pointer;
}

.btn:hover {
    background: rgb(243, 52, 52);
}