.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 70px;
  background: var(--medGrey);
  color: var(--white);
}

.content {
  display: flex;
  width: 100%;
  max-width: var(--maxWidth);
  padding: 0 20px;
}

.content span {
  font-size: var(--fontMed);
  color: var(--white);
  padding-right: 10px;
}
